@import './variable.css';

.bk-page {
    overflow: hidden;
    .icon-angle-right, .icon-angle-left {
        line-height: 36px;
        font-size: $iconNormalSize;
    }
    a {
        text-decoration: none;
    }
    .bk-page-count {
        font-size: 12px;
        margin-top: 2px;
        &-left {
            float: left;
        }
        &-right {
            float: right;
            margin-left: 12px;
        }
        > span {
            display: inline-block;
            vertical-align: middle;
        }
        .bk-select {
            min-width: 60px;
            display: inline-block;
            vertical-align: middle;
            text-align: left;
            margin: 0 4px;
            .bk-selected {
                padding: 0 22px 0 10px;
            }
        }
        .bk-total-page {
            display: inline-block;
            margin-right: 20px;
        }
    }
    .bk-page-total-count {
        font-size: 12px;
        line-height: 36px;
        color: #989dab;
        &-right {
            float: right;
            margin-left: 12px;
        }
        &-left {
            float: left;
            margin-right: 12px;
        }
        .stress {
            padding: 0 2px;
            color: #3f4046;
        }
    }
    .bk-page-selection-count {
        font-size: 12px;
        line-height: 36px;
        &-right {
            float: right;
        }
        &-left {
            float: left;
            margin-left: 12px;
        }
        .count {
            padding: 0 2px;
            font-weight: bold;
        }
    }
    &.bk-page-align {
        &-left {
            text-align: left;
        }
        &-center {
            text-align: center;
        }
        &-right {
            text-align: right;
        }
    }
    &.bk-page-small {
        .bk-page-count {
            margin-top: 0;
        }
        .page-item {
            .page-button {
                min-width: 32px;
                height: 32px;
                line-height: 30px;
            }
        }
        .bk-page-total-count,
        .bk-page-selection-count {
            line-height: 32px;
        }
        .icon-angle-right, .icon-angle-left {
            line-height: 30px;
            font-size: $iconSmallSize;
        }
    }
    &.bk-page-compact {
        .page-item {
            margin-right: -1px;
            border-radius: 0;
            &:hover {
                position: relative;
                z-index: 1;
            }
            &:first-child {
                border-radius: 2px 0 0 2px;
            }
            &:last-child {
                border-radius: 0 2px 2px 0;
            }
            &.cur-page {
                position: relative;
                z-index: 1;
            }
        }
    }
    .bk-page-list {
        margin: 0;
        padding: 0;
        overflow: hidden;
        font-size: 0;
        display: inline-block;
        vertical-align: middle;
    }
    .page-item {
        text-align: center;
        display: inline-block;
        vertical-align: middle;
        font-size: 12px;
        margin-right: 4px;
        box-sizing: border-box;
        border-radius: 2px;
        overflow: hidden;
        &.page-omit {
            border: none;
            min-width: auto;
            > span {
                display: inline-block;
            }
        }
        .page-button {
            display: block;
            min-width: 36px;
            height: 36px;
            padding: 0 4px;
            line-height: 34px;
            border: 1px solid $newGreyColor;
            color: $fnMainColor;
            background: #fff;
            cursor: pointer;
            &:hover {
                color: $newMainColor;
                border-color: $newMainColor;
            }
        }

        &.cur-page {
            .page-button {
                border-color: $newMainColor;
                color: $newMainColor;
            }
        }
        &.disabled {
            .page-button {
                border-color: $newGreyColor1;
                cursor: not-allowed;
                color: $newGreyColor;
                background-color: $newGreyColor3;
                &:hover {
                    color: $newGreyColor;
                }
            }
        }
        &:last-child {
            margin-right: 0;
        }
    }
    .bk-page-small-jump{
        display: inline-flex;
        align-items: center;
        color: #63656E;
        .btn-pre,
        .btn-next{
            font-size: 18px;
            cursor: pointer;
            transition: all .15s;
            &:hover{
                color: #3A84FF;
            }
            &.disable{
                color: #DCDEE5;
                cursor: not-allowed;
            }
            .bk-icon{
                line-height: 1;
            }
        }
        .jump-input{
            width: 36px;
            height: 28px;
            padding: 0 3px;
            margin-right: 13px;
            font-size: 14px;
            line-height: 28px;
            text-align: center;
            border: 1px solid #C4C6CC;
            border-radius: 2px;
            background-color: #fff;
            background-image: none;
            transition: all .15s;
            &:hover,
            &:focus{
                border-color: #3A84FF;
            }
        }
        .page-total{
            font-size: 14px;
            vertical-align: top;
        }
    }
}
